import React from "react";
import { Box, Grid, Hidden, Typography } from "@mui/material";
import Modal from '../../components/Modal';
import { useTranslation } from 'react-i18next'

export default function MobilePassView(props) {
    const { t } = useTranslation();


    const renderContent = () => {
        return (
                <Box width={'100%'}>
                    <Grid item xs={12} textAlign='center' marginBottom={2}>
                        <center><Typography variant='h3'>{t('mobile_pass_card')}</Typography></center>
                    </Grid>
                    <Grid item xs={12} textAlign='center'>
                        <center><Typography variant='h7'>{t('mobile_pass_available_desc')}</Typography></center>
                    </Grid>
                </Box>
        )
    }
    const renderRoot = () => {
        return (
            <>
                <Hidden mdDown>
                    <Modal isOpen={props.open} onClose={props.onClose} enableCloseButton={true}>
                        {renderContent()}
                    </Modal>
                </Hidden>
                <Hidden mdUp>
                    <Modal isOpen={props.open} onClose={props.onClose} enableCloseButton={true} width={'100%'}>
                        {renderContent()}
                    </Modal>
                </Hidden>
            </>
        )
    }
    return (
        <>{renderRoot()}</>
    )
}